  mounted() {
    let that = this;
    that.initlj();
    window.sessionStorage.removeItem('isplay');
    this.mplayer = document.getElementById('videoID');
    //控制loading动画
    setInterval(() => {
      if (this.dotarr.length == 1) {
        this.dotarr = ['1', '1'];
      } else if (this.dotarr.length == 2) {
        this.dotarr = ['1', '1', '1'];
      } else if (this.dotarr.length == 3) {
        this.dotarr = ['1'];
      }
    }, 300);

    setInterval(() => {
      this.ischeckloading = !this.ischeckloading;
    }, 300);

    that.layer1.on('touchstart', function (evt) {
      if (!window.sessionStorage.getItem('isplay') && document.getElementById('audio3')) {
        window.sessionStorage.setItem('isplay', 1);

        document.getElementById('audio3').play();
      }
      var name = evt.target.attrs.name;
    });
    var starty2 = 0;
    that.layer2.on('dragstart', function (evt) {
      starty2 = evt.evt.changedTouches[0].clientY;
      console.log(888);
    });
    that.layer2.on('touchstart', function (evt) {
      var startyend2 = evt.evt.changedTouches[0].clientY;
      setTimeout(() => {
        that.isshow = 5;
      }, 1000);
    });
    that.layer1.on('dragend', function (evt) {
      that.selectAct = 0;
      var name = evt.target.attrs.name;
      that.deal2(name, evt);
    });
    that.layer1.on('touchend', function (evt) {
      that.selectAct = 0;
      if (!window.sessionStorage.getItem('isplay') && document.getElementById('audio3')) {
        window.sessionStorage.setItem('isplay', 1);

        document.getElementById('audio3').play();
      }
    });
