      var x2 = 0;

      var y1 = 0;
      var x3 = 0;
      var y2 = 0;
      that.deal(name, evt);

      that.layer1.draw();
      that.layer2.draw();
    });
    this.mplayer.addEventListener('play', () => {
      this.mplay();
    });
    this.mplayer.addEventListener('pause', () => {
      this.mpause();
    });
